export default {
	data() {
		return {
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar
		};
	},
	name: 'cu-custom',
	computed: {
		style() {
			const {StatusBar,CustomBar} = this;
			return `height:${CustomBar}px;padding-top:${StatusBar}px;`;
		},
		backgroundColor(){
			return `background-color:${this.bgColor}`
		}
	},
	props: {
		bgColor:{
			type: String,
			default: ""	
		},
		isBack: {
			type: [Boolean, String],
			default: false
		},
		bgImage: {
			type: String,
			default: ''
		},
		shadow: {
			type: Boolean,
			default: true
		},
		fixed: {
			type: Boolean,
			default: true
		}
	},
	methods: {
		BackPage() {
			if (this.isBack) {
				uni.navigateBack({
					delta: 1
				});
			}
		}
	}
};
